'use client';

import React, { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import {
    ArrowLeft,
    Package,
    Truck,
    CreditCard,
    User,
    MapPin,
    Calendar,
    Clock,
    CheckCircle,
    XCircle,
    AlertCircle,
    Info,
    ChevronRight,
    ExternalLink
} from 'lucide-react';

interface OrderDetails {
    order: {
        id: string;
        customer_name: string;
        customer_phone: string;
        customer_email: string;
        address_full: string;
        address_note: string;
        items: any[];
        subtotal: number;
        delivery_fee: number;
        discount: number;
        total: number;
        status: string;
        delivery_mode: string;
        payment_method: string;
        created_at: string;
    };
    payment?: {
        status: string;
        amount: number;
        currency: string;
        moneroo_payment_id: string;
    };
    delivery?: {
        status: string;
        address_full: string;
        delivery_fee: number;
    };
}

const STATUS_COLORS: Record<string, string> = {
    pending: 'bg-yellow-100 text-yellow-800 border-yellow-200',
    confirmed: 'bg-blue-100 text-blue-800 border-blue-200',
    processing: 'bg-purple-100 text-purple-800 border-purple-200',
    shipped: 'bg-indigo-100 text-indigo-800 border-indigo-200',
    delivered: 'bg-teal-100 text-teal-800 border-teal-200',
    completed: 'bg-green-100 text-green-800 border-green-200',
    cancelled: 'bg-red-100 text-red-800 border-red-200'
};

const STATUS_LABELS: Record<string, string> = {
    pending: 'En attente',
    confirmed: 'Confirmée',
    processing: 'En traitement',
    shipped: 'Expédiée',
    delivered: 'Livrée',
    completed: 'Terminée',
    cancelled: 'Annulée'
};

export default function OrderDetailsPage() {
    const params = useParams();
    const router = useRouter();
    const [data, setData] = useState<OrderDetails | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [isUpdating, setIsUpdating] = useState(false);

    useEffect(() => {
        if (params && params.id) {
            fetchOrderDetails();
        }
    }, [params]);

    const [errorData, setErrorData] = useState<any>(null);

    const fetchOrderDetails = async () => {
        try {
            const token = localStorage.getItem('admin_token');
            const url = `http://localhost:3333/api/admin/orders/${params.id}`;
            console.log('Fetching order details from:', url);

            const response = await fetch(url, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });

            if (response.ok) {
                const result = await response.json();
                console.log('Order details fetched successfully');
                setData(result);
                setErrorData(null);
            } else {
                const err = await response.json();
                setErrorData(err);
                setData(null);
            }
        } catch (error) {
            console.error('Network error fetching order details:', error);
            setData(null);
        } finally {
            setIsLoading(false);
        }
    };

    const updateStatus = async (newStatus: string) => {
        setIsUpdating(true);
        try {
            const token = localStorage.getItem('admin_token');
            const response = await fetch(`http://localhost:3333/api/admin/orders/${params.id}/status`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ status: newStatus })
            });
            if (response.ok) {
                await fetchOrderDetails();
            } else {
                const error = await response.json();
                alert(`Erreur: ${error.error || 'Impossible de mettre à jour le statut'}`);
            }
        } catch (error) {
            console.error('Failed to update status:', error);
            alert('Erreur réseau');
        } finally {
            setIsUpdating(false);
        }
    };

    if (isLoading) {
        return (
            <div className="flex items-center justify-center min-h-[400px]">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-black"></div>
            </div>
        );
    }

    if (!data) {
        return (
            <div className="bg-white rounded-3xl p-12 text-center shadow-premium space-y-4 max-w-2xl mx-auto border border-gray-100">
                <div className="w-20 h-20 bg-red-50 text-red-500 rounded-3xl flex items-center justify-center mx-auto mb-6">
                    <AlertCircle className="w-10 h-10" />
                </div>
                <h2 className="text-3xl font-black text-gray-900 tracking-tight">Commande introuvable</h2>
                <div className="bg-gray-50 p-4 rounded-2xl text-xs font-mono text-gray-500 break-all mb-4 space-y-2">
                    <p>ID recherché : {params.id as string}</p>
                    {errorData && (
                        <div className="pt-2 border-t border-gray-200">
                            <p>Total commandes en base : {errorData.dbStats?.count}</p>
                            <p className="mt-1">Derniers IDs :</p>
                            <ul className="list-disc list-inside opacity-70">
                                {errorData.sampleIds?.map((id: string) => (
                                    <li key={id}>{id.substring(0, 8)}...</li>
                                ))}
                            </ul>
                        </div>
                    )}
                </div>

                <p className="text-gray-500">
                    L'identifiant de commande spécifié n'existe pas ou le serveur est inaccessible.
                </p>
                <div className="flex flex-col sm:flex-row gap-3 pt-6 justify-center">
                    <button
                        onClick={() => router.push('/admin/orders')}
                        className="px-6 py-3 bg-black text-white rounded-2xl font-bold hover:bg-gray-800 transition-all flex items-center gap-2"
                    >
                        <ArrowLeft className="w-4 h-4" /> Liste des commandes
                    </button>
                    <button
                        onClick={() => fetchOrderDetails()}
                        className="px-6 py-3 bg-gray-100 text-gray-900 rounded-2xl font-bold hover:bg-gray-200 transition-all"
                    >
                        Réessayer
                    </button>
                </div>
            </div>
        );
    }

    const { order, payment, delivery } = data;
    const items = typeof order.items === 'string' ? JSON.parse(order.items) : order.items;

    return (
        <div className="space-y-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
            {/* Header section... (keeping the rest same as previous good version) */}
            <div className="flex items-center gap-2 text-sm text-gray-400 font-medium">
                <button onClick={() => router.push('/admin/orders')} className="hover:text-black transition-colors">Commandes</button>
                <ChevronRight className="w-4 h-4" />
                <span className="text-black">{order.id.substring(0, 8)}</span>
            </div>

            <div className="bg-white rounded-[2.5rem] p-8 border border-gray-100 shadow-premium flex flex-col md:flex-row md:items-center justify-between gap-6">
                <div className="flex items-center gap-6">
                    <div className={`w-16 h-16 rounded-2xl flex items-center justify-center border-2 ${STATUS_COLORS[order.status]}`}>
                        <Package className="w-8 h-8" />
                    </div>
                    <div>
                        <div className="flex items-center gap-3 flex-wrap">
                            <h1 className="text-3xl font-black tracking-tight text-gray-900">Commande #{order.id.substring(0, 8)}</h1>
                            <span className={`px-4 py-1 rounded-full text-[10px] font-black uppercase tracking-widest border-2 ${STATUS_COLORS[order.status]}`}>
                                {STATUS_LABELS[order.status] || order.status}
                            </span>
                        </div>
                        <p className="text-gray-400 font-medium mt-1 flex items-center gap-2">
                            <Calendar className="w-4 h-4" />
                            {new Date(order.created_at).toLocaleDateString('fr-FR', {
                                day: 'numeric',
                                month: 'long',
                                year: 'numeric',
                                hour: '2-digit',
                                minute: '2-digit'
                            })}
                        </p>
                    </div>
                </div>

                <div className="flex flex-wrap gap-3">
                    {order.status === 'pending' && (
                        <button
                            disabled={isUpdating}
                            onClick={() => updateStatus('confirmed')}
                            className="px-6 py-3 bg-black text-white rounded-2xl font-bold text-sm hover:scale-105 active:scale-95 transition-all disabled:opacity-50 flex items-center gap-2 shadow-lg shadow-black/10"
                        >
                            <CheckCircle className="w-4 h-4" /> Confirmer la commande
                        </button>
                    )}
                    {['confirmed', 'processing'].includes(order.status) && (
                        <button
                            disabled={isUpdating}
                            onClick={() => updateStatus('shipped')}
                            className="px-6 py-3 bg-[#007AFF] text-white rounded-2xl font-bold text-sm hover:scale-105 active:scale-95 transition-all disabled:opacity-50 flex items-center gap-2 shadow-lg shadow-blue-500/20"
                        >
                            <Truck className="w-4 h-4" /> Marquer comme expédié
                        </button>
                    )}
                    {order.status === 'shipped' && (
                        <button
                            disabled={isUpdating}
                            onClick={() => updateStatus('delivered')}
                            className="px-6 py-3 bg-[#34C759] text-white rounded-2xl font-bold text-sm hover:scale-105 active:scale-95 transition-all disabled:opacity-50 flex items-center gap-2 shadow-lg shadow-green-500/20"
                        >
                            <CheckCircle className="w-4 h-4" /> Livré avec succès
                        </button>
                    )}
                    {order.status !== 'cancelled' && order.status !== 'completed' && order.status !== 'delivered' && (
                        <button
                            disabled={isUpdating}
                            onClick={() => updateStatus('cancelled')}
                            className="px-4 py-3 bg-red-50 text-red-600 rounded-2xl font-bold text-sm hover:bg-red-100 transition-all disabled:opacity-50 flex items-center gap-2"
                        >
                            <XCircle className="w-4 h-4" /> Annuler
                        </button>
                    )}
                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                {/* Main Content Areas... */}
                <div className="lg:col-span-2 space-y-8">
                    <div className="bg-white rounded-[2rem] shadow-premium border border-gray-100 overflow-hidden">
                        <div className="px-8 py-6 bg-gray-50/50 border-b border-gray-100 flex items-center justify-between">
                            <h2 className="font-bold flex items-center gap-3 text-gray-900">Articles</h2>
                        </div>
                        <div className="divide-y divide-gray-50 px-4">
                            {items.map((item: any, idx: number) => (
                                <div key={idx} className="p-4 flex items-center justify-between group">
                                    <div className="flex items-center gap-6">
                                        <div className="w-20 h-20 bg-gray-50 rounded-2xl border border-gray-100 flex items-center justify-center p-2 group-hover:scale-105 transition-transform duration-300">
                                            <Package className="w-8 h-8 text-gray-200" />
                                        </div>
                                        <div>
                                            <p className="font-black text-gray-900 text-lg uppercase tracking-tight">{item.name}</p>
                                            <p className="text-sm font-bold text-gray-400">{item.price?.toLocaleString()} F x {item.quantity}</p>
                                        </div>
                                    </div>
                                    <p className="font-black text-xl text-gray-900">{(item.price * item.quantity).toLocaleString()} F</p>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>

                <div className="space-y-8">
                    <div className="bg-white rounded-[2rem] p-8 border border-gray-100 shadow-premium">
                        <h2 className="text-xl font-black text-gray-900 flex items-center gap-3 mb-6">Client</h2>
                        <div className="space-y-2">
                            <p className="font-black text-lg">{order.customer_name}</p>
                            <p className="text-sm font-bold text-gray-500">{order.customer_phone}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
